<template>
    <div>
	<table>
	    <thead>
		<tr>
		    <th>Tipo</th>
		    <th>Direccion</th>
		    <th>Propietario</th>
		    <th>Localidad</th>
		</tr>
	    </thead>
	    <tbody>
		<tr v-for="propiedad in propiedades">
		    <td>{{propiedad.tipo}}</td>
		    <td>{{propiedad.direccion}}</td>
		    <td>{{propiedad.propietario}}</td>
		    <td>{{propiedad.localidad}}</td>
		</tr>
	    </tbody>
	</table>
    </div>
</template>

<script>
 import axios from 'axios';
 export default {
     data() {
	 return {
	     propiedades:null,
	 }
     },

     methods:{
	 getPropiedades(){
	     axios.get('http://localhost:8000/martinez/servicios/api/propiedades')
		  .then(res=>this.propiedades=res.data.rows)
	 }
     },
     mounted(){
	 this.getPropiedades();
     }
 }

</script>



